'use strict';
import React, { Component } from 'react';
import { observer } from 'mobx-react';
import keydown from 'react-keydown';
import PongApi from './PongApi';
import './styles/pong.scss';
const io = require('socket.io-client');
const socket = io.connect('http://localhost:5000');
const movementSize = 18;

@observer
class PongContainer extends Component {


	constructor(props) {
		super(props);

		socket.on('ballMoved', (data) => {
			this.respondToBallMove(data);
		});

		socket.on('paddle1Moved', (data) => {
			this.movePaddle1(data);
		});

		socket.on('paddle2Moved', (data) => {
			this.movePaddle2(data);
		});

		socket.on('scoreChanged', (data) => {
			this.scoreChanged(data);
		})

		socket.on('winner', (data) => {
			this.displayWinner(data);
		})
	}

	displayWinner(data) {
		this.props.store.endGame(data.winner);
	}

	respondToBallMove(data) {
		this.props.store.moveBall(data);
	}

	scoreChanged(data) {
		this.props.store.setScore(data);
	}

	movePaddle1(data) {
		if (this.props.store.paddleNumber != 1) {
			this.props.store.setPaddle(1, data.position);
		}
	}

	movePaddle2(data) {
		if (this.props.store.paddleNumber != 2) {
			this.props.store.setPaddle(2, data.position);
		}
	}
	
	// Listens for up arrow key
	// Moves the paddle on the client side, emits a signal for the server to move the paddle.
	@keydown(38)
	moveUp(event) {
		store = this.props.store;
	    var myPosition = store.paddleNumber === 1 ? store.paddle1Position : store.paddle2Position;
	    if (myPosition <= 35 + movementSize) {
	    	store.movePaddle(store.paddleNumber, -(myPosition - 35));
	    } else {
		    store.movePaddle(store.paddleNumber, -movementSize);
	    }

	    socket.emit('movePaddle', {
		    	player: store.paddleNumber,
		    	position: store.paddleNumber === 1 ? store.paddle1Position : store.paddle2Position,
		    	id: store.id
		    });
	}

	// Listens for down arrow key
	// Moves the paddle on the client side, emits a signal for the server to move the paddle.
	@keydown(40)
	moveDown(event) {
		store = this.props.store;
	    var myPosition = store.paddleNumber === 1 ? store.paddle1Position : store.paddle2Position;
	    if (myPosition >= 365 - movementSize) {
	    	store.movePaddle(store.paddleNumber, 365 - myPosition);
	    } else {
		    store.movePaddle(store.paddleNumber, movementSize);
	    }

	    socket.emit('movePaddle', {
		    	player: store.paddleNumber,
		    	position: store.paddleNumber === 1 ? store.paddle1Position : store.paddle2Position,
		    	id: store.id
		    });
	}

	// React calls the componentDidMount function one time only, when the component is first initialized.
	// In this method, I retrieve the current score and my player number, if any.
	// Any players after the first 2 will be spectators.
	componentDidMount(newProps) {
		PongApi.getScore()
		.then((data) => {
			this.props.store.setScore(data);
		});

		PongApi.getPlayer()
		.then((data) => {
			if (data.player != null && data.id != null) {
				this.props.store.setCurrentPlayer(data.player);
				this.props.store.setID(data.id);
			} else {
				this.props.store.setCurrentPlayer(null);
			}
		});
	}

	// Render is call each time one of the props is changed.
	// Using the @observer and @observable annotations from MobX,
	// React will automatically know when properties of the global state are changed,
	// and it will re-render when necessary.
	render() {
		var paddle1Style = {
			marginTop: -35 + this.props.store.paddle1Position,
			marginBottom: 265 - this.props.store.paddle1Position,
		}

		var paddle2Style = {
			marginTop: -35 + this.props.store.paddle2Position,
			marginBottom: 265 - this.props.store.paddle2Position,
		}

		var ballStyle = {
			left: this.props.store.xPosition,
			top: this.props.store.yPosition
		}

		if(this.props.store.gameOver) {
			return (
				<div className="gameBoard">
					<p> {"Winner: " + this.props.store.winner} </p>
				</div>
			);
		} else {
			return (
				<div className="container"> 
					<div className="gameBoard">
						<div className="paddle" style={paddle1Style}></div>
						<div id="paddleB" className="paddle" style={paddle2Style}></div>
						<div className="ball" style={ballStyle} ></div>
						<p> {"Player 1 score: " + this.props.store.score.player1score} </p>
						<p> {"Player 2 score: " + this.props.store.score.player2score} </p>
					</div>
				</div>
			);
		}
	}
}

export default PongContainer;


