import { observable, autorun, computed } from 'mobx';

// I built this app using MobX to maintain a global store.
// This would be really helpful later on if we added other
// components to the page that needed to share properties of the store.

// However, I ended up implementing the app with a single component, PongContainer.js
// Therefore, this store is a little excessive.

// If I were to stop building this app now, I would move this store to be part of
// the component's local state.
class PongStore {
  @observable paddleNumber = null;
  @observable score = {
    player1score: 0,
    player2score: 0
  }
  @observable xPosition = 250;
  @observable yPosition = 200;
  @observable paddle1Position = 200;
  @observable paddle2Position = 200;
  @observable id = null;
  @observable gameOver = false;
  @observable winner;

    movePaddle(paddleNumber, diff) {
      if (!paddleNumber || (paddleNumber != 1 && paddleNumber != 2) || !diff) {
        return;
      }

      if (paddleNumber === 1) {
        this.paddle1Position = this.paddle1Position + diff;
      } else {
        this.paddle2Position = this.paddle2Position + diff;
      }
    }

    endGame(winner) {
      this.winner = winner;
      this.gameOver = true;
    }

    setPaddle(paddleNumber, position) {
      if (!paddleNumber || (paddleNumber != 1 && paddleNumber != 2) || !position) {
        return;
      }

      if (paddleNumber === 1) {
        this.paddle1Position = position;
      } else {
        this.paddle2Position = position;
      }
    }

    moveBall(newPosition) {
      if (!newPosition || !newPosition.xPosition || !newPosition.yPosition) {
        return;
      }

      this.xPosition = newPosition.xPosition;
      this.yPosition = newPosition.yPosition;
    }

    setCurrentPlayer(playerNumber) {
      if (!playerNumber || (playerNumber != 1 && playerNumber != 2)) {
        return;
      }
      this.paddleNumber = playerNumber;
    }

    setScore(newScore) {
      if (!newScore) {
        return;
      }

      this.score = newScore;
    }

    setID(id) {
      this.id = id;
    }
}

var store = window.store = new PongStore

export default store;


